<template>
  <div>
    <el-dialog title="模板字段--修改" :visible.sync="dialogFormVisible" @close="handleClose">
      <el-form :model="editData" size="small" ref="editData">
        <el-form-item label="数据类型" :label-width="formLabelWidth">
          <el-radio-group v-model="editData.type" @change="switchChange">
            <el-radio :label="1">商品字段值</el-radio>
            <el-radio :label="0">固定值</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="字段值" :label-width="formLabelWidth">
          <el-select v-model="editData.value" placeholder="请选择字段值" :disabled="selectDisabled">
            <el-option
              v-for="(item,index) in fieldList"
              :key="index"
              :label="item.name"
              :value="item.name"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="固定值" :label-width="formLabelWidth">
          <el-input v-model="editData.valueTwo" autocomplete="off" :disabled="!selectDisabled"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button size="small" type="primary" @click="submitForm('editData')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogFormVisible: this.$store.state.changeFieldStatus,
      formLabelWidth: "100px",
      selectDisabled: true,
      editData: {
        id: "",
        type: "",
        field: "",
        value: "",
        valueTwo: ""
      },
      value: "",
      fieldList: []
    };
  },
  methods: {
    //开关滑块
    switchChange(value) {
      if (value == "0") {
        this.selectDisabled = true;
        // this.editData.value = "";
        // this.editData.valueTwo = this.SelectValue;
      }
      if (value == "1") {
        this.selectDisabled = false;
        // this.editData.valueTwo = "";
        // this.editData.value = this.SelectValue;
      }
    },
    handleClose() {
      this.$store.commit("CHANGE_FIELD_STATUS", false);
      this.$store.commit("CHANGE_FIELD_DATA", {
        id: "",
        type: 0,
        field: "",
        value: "",
        valueTwo: ""
      });
      this.editData.value = "";
    },
    //添加前校验
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.postRequest(`/v1/home/index/edit_field`, this.editData).then(
            res => {
              if (res.data.code == "200") {
                this.$message({
                  type: "success",
                  message: "修改成功"
                });
                this.handleClose();
                this.$store.commit("EXCEL_RIGHT_REFRESH");
              }
            }
          );
        } else {
          return false;
        }
      });
    },
    //获取字段值
    getFieldData() {
      this.postRequest(`/v1/home/index/goods_fields`).then(res => {
        if (res.data.code == "200") {
          this.fieldList = res.data.data;
        }
      });
    }
    //  changeFieldData1(){
    // }
  },
  created() {
    this.getFieldData();
  },
  computed: {
    excelId() {
      return this.$store.state.excelId;
    },
    changeFieldStatus() {
      return this.$store.state.changeFieldStatus;
    },
    changeFieldData() {
      return this.$store.state.changeFieldData;
    }
  },
  watch: {
    changeFieldStatus: function(newVal, oldVal) {
      this.dialogFormVisible = newVal;
    },
    changeFieldData: function(newVal, oldVal) {
      this.SelectValue = newVal.value;
      this.editData.field = newVal.field;
      this.editData.type = newVal.type;
      if (newVal.type == "1") {
        this.selectDisabled = false;
        this.editData.value = newVal.value;
      } else {
        this.selectDisabled = true;
        this.editData.valueTwo = newVal.value;
      }
    },
    excelId: function(newVal, oldVal) {
      this.editData.id = newVal;
    }
  }
};
</script>
<style lang="scss" scoped>
div {
  text-align: left;
}
</style>
