/*excel模板左边组件*/
<template>
  <div class="mainLeft">
    <div class="mainLeftTop">
      <el-button type="success" size="mini" @click="handleClickaddExcel"><i class="el-icon-plus "></i>添加</el-button>
      <el-button type="warning" size="mini" @click="handleClickEditExcel"><i class="el-icon-edit "></i>修改</el-button>
      <el-button type="danger" size="mini" @click="handleClickDelExcel"><i class="el-icon-close "></i>删除</el-button>
      <el-button type="success" size="mini" @click="handleClickCopy"><i class="iconfont icon_fuzhi "></i>复制</el-button>
      <el-button type="primary" size="mini" @click="handleClickDownLoad"><i class="el-icon-download "></i>下载</el-button>
      <el-button type="primary" icon="el-icon-refresh" circle size="mini"></el-button>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[50, 100, 200]"
        :page-size="pageSize"
        :pager-count="pagerCount"
        class="disInlineBlock"
        layout="sizes, prev, pager, next"
        :total="total">
      </el-pagination>
      <div class="common">
        共{{ total }}笔
      </div>
    </div>
    <el-table
      ref="singleTable"
      :data="tableData"
      highlight-current-row
      height="82vh"
      border
      stripe
      @row-dblclick="dbClickRow"
      @row-click="handleRowClick"
      @selection-change="handleSelectionChange"
      v-loading="loading"
      style="width: 100%">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        type="selection"
        width="50">
      </el-table-column>
      <el-table-column
        label="类型"
        min-width="60"
        show-overflow-tooltip>
        <template slot-scope="scope">
          {{ scope.row.type == '1' ? '个人' : '公共' }}
        </template>
      </el-table-column>
      <el-table-column
        property="name"
        label="名称"
        sortable
        min-width="240">
      </el-table-column>
      <el-table-column
        label="状态"
        min-width="50">
        <template slot-scope="scope">
          <el-tooltip class="item" effect="dark" content="启用" placement="top">
            <i class="el-icon-check colorGreen" v-if="scope.row.status==1"></i>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="禁用" placement="top">
            <i class="el-icon-close colorRed" v-if="!scope.row.status==1"></i>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column
        property="update_time"
        label="时间"
        sortable
        show-overflow-tooltip
        min-width="150">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data(){
    return {
      total: 0,
      productNum: 120,
      currentPage: 1,//第几页
      pagerCount: 5,//按钮数量
      pageSize: 50,//每页多少条数据
      name: '', //搜索值
      tableData: [],
      multipleSelection: [],
      loading: false,
    }
  },
  methods: {
    //每页多少条
    handleSizeChange(val) {
      this.initData(val,this.currentPage,this.name);
    },
    //第几页
    handleCurrentChange(val) {
      this.initData(this.pageSize,val,this.name);
    },
    //某一行被点击时
    handleRowClick(row){
      this.$store.commit('CHANGE_EXCEL_ID',row.id);
    },
    //复制
    handleClickCopy(){
      if(this.multipleSelection.length!=1){
        this.$message({
          type: 'warning',
          message: '选择一条记录进行复制'
        })
        return false;
      }
      this.postRequest(`/v1/home/index/copyExcel?id=${this.multipleSelection[0].id}`)
      .then(res => {
        if(res.data.code == '200'){
          this.$store.commit('REFRESH_EXCEL_STATUS');
          this.$message({
            type: 'success',
            message: '复制成功'
          })
        }
      })
    },
    //某一行被双击时
    dbClickRow(row){
      this.$refs.singleTable.clearSelection();
      this.$refs.singleTable.toggleRowSelection(row);
      this.$store.commit('CHANGE_EXCEL_DATA',row);
      this.$store.commit('CHANGE_EXCEL_EDIT_STATUS','edit');
      this.$store.commit('ADD_EXCEL_SHOW_STATE',true);
    },
    //多选
    handleSelectionChange(val){
      this.multipleSelection = val;
    },
    //删除模板前进行校验
    beforeDelExcel(){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.delExcel();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    },
    //添加模板
    handleClickaddExcel(){
      this.$store.commit('CHANGE_EXCEL_DATA',{ name: '', size: '1200', status: 1, excel: '' });
      this.$store.commit('CHANGE_EXCEL_EDIT_STATUS','add');
      this.$store.commit('ADD_EXCEL_SHOW_STATE',true);
    },
    //修改模板信息
    handleClickEditExcel(){
      if(this.multipleSelection.length==1){
        this.$store.commit('CHANGE_EXCEL_DATA',this.multipleSelection[0]);
        this.$store.commit('CHANGE_EXCEL_EDIT_STATUS','edit');
        this.$store.commit('ADD_EXCEL_SHOW_STATE',true);
      }else if(this.multipleSelection.length>1){
        this.$message({
          type: 'warning',
          message: '只能选择一条记录进行修改'
        })
      }else {
        this.$message({
          type: 'warning',
          message: '请选择需要修改的记录'
        })
      }
    },
    //删除excel钱判断是否为公共模板
    handleClickDelExcel(){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let idArr = [];
          for (let index = 0; index < this.multipleSelection.length; index++) {
            const element = this.multipleSelection[index];
            if(element.type!='1'){
              idArr = [];
              this.$message({
                type:'warning',
                message: '公共模板不能删除'
              })
              return false;
            }else {
              idArr.push(element.id);
            }
          }
          this.delExcel(idArr);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    //删除excel模板文件
    delExcel(data){
      this.postRequest(`/v1/home/index/excelDelete?id=${data.join(',')}`)
      .then(res => {
        if(res.data.code == '200'){
          this.$message({
            type: 'success',
            message: '删除成功'
          })
          this.initData(this.pageSize,this.currentPage,this.name);
        }else {
          this.$message.error('失败');
        }
      })
    },
    //下载excel模板
    handleClickDownLoad(){
      if(this.multipleSelection.length!=1){
        this.$message({
          type: 'warning',
          message: '选择一条记录进行下载'
        })
      }else {
        // window.location.href = `/v1/home/index/excelExport?id=${this.multipleSelection[0].id}`
        this.postRequest(`/v1/home/index/excelExport?id=${this.multipleSelection[0].id}`)
        .then(res => {
          if(res.data.code=='200'){
            window.location.href = res.data.data;
          }
        })
      }
    },
    //初始化数据
    initData(limit,page,name){
      this.loading = true;
      this.postRequest(`/v1/excelAll?limit=${limit}&name=${name}&page=${page}`)
      .then(res => {
        if(res.data.code=='200'){
          this.loading = false;
          this.tableData = res.data.data.data;
          this.total = res.data.data.total;
          this.$store.commit('CHANGE_EXCEL_ID',this.tableData[0].id);
        }
      })
      .catch(error => {
      })
    }
  },
  created(){
    this.initData(this.pageSize,this.currentPage,this.name);
  },
  computed: {
    excelSearchVal(){
      return this.$store.state.excelSearchVal;
    },
    refreshExcelStatus(){
      return this.$store.state.refreshExcelStatus;
    }
  },
  watch: {
    excelSearchVal: function(newVal,oldVal){
      this.name = newVal;
      this.initData(this.pageSize,this.currentPage,this.name);
    },
    refreshExcelStatus: function(newVal,oldVal){
      this.initData(this.pageSize,this.currentPage,this.name);
    },
  },
}
</script>

<style lang="scss" scoped>
.mainLeft {
  position: fixed;
  top: 12vh;
  right: 45%;
  display: inline-block;
  width: 55%;
  text-align: left;
  .mainLeftTop {
    height: 4vh;
    line-height: 4vh;
    padding: .5vh 0;
    overflow: auto;
    i {
      margin-right: 5px;
    }
    .common {
      width: 50px;
      text-align: right;
      display: inline-block;
      right: 25px;
      top: 7vh;
    }
  }
}
.el-pagination {
  display: inline-block;
  line-height: 22px;
}
</style>
