/*excel模板左边组件*/
<template>
  <div class="mainLeft">
    <div class="mainLeftTop">
      <span class="excelTitle">Excel模板字段列表</span>
      <el-button type="warning" size="mini" @click="handleCLickChange"><i class="el-icon-edit"></i>修改</el-button>
      <div class="common">
        共{{ productNum }}笔
      </div>
    </div>
    <div class="inputList">
      <el-input size="small" v-model="sendData.column_alias" placeholder="列" class="width30 marginLeft2" @change="inputChange"></el-input>
      <el-input size="small" v-model="sendData.field" placeholder="字段名称" class="width30 marginLeft2" @change="inputChange"></el-input>
      <el-input size="small" v-model="sendData.value" placeholder="对应值/字段" class="width30 marginLeft2" @change="inputChange"></el-input>
    </div>
    <el-table
      v-loading="loading"
      ref="singleTable"
      :data="tableData"
      highlight-current-row
      border
      height="82vh"
      stripe
      @row-click="rowClick"
      @row-dblclick="rowDblClick"
      style="width: 100%">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        property="column_alias"
        label="列"
        min-width="60"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="field"
        label="字段名称"
        show-overflow-tooltip
        min-width="200">
      </el-table-column>
      <el-table-column
        property="value"
        label="对应值/字段"
        show-overflow-tooltip
        min-width="180">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data(){
    return {
      productNum: 0,
      tableData: [],
      loading: false,
      multipleSelection: [],
      sendData: {
        value: '',
        column_alias: '',
        field: ''
      },
    }
  },
  methods: {
    // filterHandler(value, row, column) {
    //   const property = column['property'];
    //   return row[property] === value;
    // },
    inputChange(value){
      this.initData();
    },
    //某一行点击时
    rowClick(data){
      this.multipleSelection = [];
      this.multipleSelection.push(data);
    },
    // handleClose(){
    //   this.editData = {};
    // },  
    //某一行双击
    rowDblClick(data){
      let selectType = sessionStorage.setItem('typeSel',data.type);
      this.rowClick(data);
      this.handleCLickChange();
    },
    //修改弹出框
    handleCLickChange(){
      if(this.multipleSelection.length!=1){
        this.$message({
          type: 'warning',
          message: '选择一条记录进行修改',
        })
      }else {
        this.$store.commit('CHANGE_FIELD_DATA',this.multipleSelection[0]);
        this.$store.commit('CHANGE_FIELD_STATUS',true);
        // ;
      }
    },
    //获得该Excel的数据
    initData(id=sessionStorage.getItem('excelId'), sendData=this.sendData){
      this.loading = true;
      let data = Object.assign({id}, sendData);
      this.postRequest(`/v1/home/index/check`,data)
      .then(res => {
        if(res.data.code=='200'){
          this.tableData = res.data.data.data;
          this.productNum = res.data.data.count;
        }
        this.loading = false;
      })
    }
  },
  computed: {
    excelId(){
      return this.$store.state.excelId;
    },
    excelRightRefresh(){
      return this.$store.state.excelRightRefresh;
    }
  },
  watch: {
    excelId: function(newVal,oldVal){
      this.initData(newVal);
    },
    excelRightRefresh: function(newVal,oldVal){
      this.initData(sessionStorage.getItem('excelId')?sessionStorage.getItem('excelId'):'');
    },
  }
}
</script>

<style lang="scss" scoped>
.mainLeft {
  position: fixed;
  top: 12vh;
  right: 0;
  display: inline-block;
  width: 45%;
  text-align: left;
  .mainLeftTop {
    height: 4vh;
    line-height: 4vh;
    padding: .5vh 0;
    overflow: auto;
    .excelTitle {
      font-weight: 500;
    }
    i {
      margin-right: 5px;
    }
    .common {
      width: 50px;
      text-align: right;
      display: inline-block;
      right: 25px;
      top: 12.5vh;
      position: fixed;
    }
  }
  .inputList {
    padding: 5px 0;
  }
}
</style>
