<template>
  <div class="topCon">
    <span>关键词：</span>
    <el-input size="mini" placeholder="搜索：模板名称" v-model="searchValue" clearable class="disInlineBlock width20"></el-input>
    <el-button type="primary" size="small" @click="clickSearch"><i class="el-icon-search"></i>查询</el-button>
    <el-button type="warning" size="small" @click="clickRefresh"><i class="el-icon-refresh"></i>重置</el-button>
  </div>
</template>

<script>
export default {
  data(){
    return {
      searchValue: ''
    }
  },
  methods: {
    clickSearch(){
      this.$store.commit('EXCEL_SEARCH_VALUE',this.searchValue);
    },
    clickRefresh(){
      this.searchValue = '';
    }
  }
}
</script>

