<template>
  <div class="addExcelStyle">
    <el-dialog title="编辑Excel模板" :visible.sync="dialogFormVisible" @close="handleClose">
      <el-form :model="excel" size="mini" ref="excel" :rules="rules">
        <el-form-item label="模板文件：" :label-width="formLabelWidth" prop="name">
          <el-input :placeholder="placeholder" v-model="excel.name" disabled class="disInlineBlock width65">
          </el-input>
          <uploadExcel :uploadData="uploadData" class="width80px" />
        </el-form-item>
        <el-form-item label="模板名称：" :label-width="formLabelWidth" prop="name">
          <el-input :placeholder="placeholder" v-model="excel.name"></el-input>
        </el-form-item>
        <el-form-item label="图片尺寸：" :label-width="formLabelWidth" prop="size">
          <el-input :placeholder="placeholder" v-model="excel.size"></el-input>
          <span>单位：px，限定图片输出宽度</span>
        </el-form-item>
        <el-form-item label="启用状态：" :label-width="formLabelWidth">
          <el-radio-group v-model="excel.status">
            <el-radio :label="1">启用</el-radio>
            <el-radio :label="0">不启用</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="说明：" :label-width="formLabelWidth">
          <p>表格文件系统只支持.xls 和 .xlsx格式的文件，
            首选是使用.xls，如果上传不成功，请使用.xlsx格式！</p>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button size="small"  :loading="buttonLoading" type="primary" @click="submitForm('excel')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import uploadExcel from '@/components/uploadExcel'
export default {
  data(){
    return {
      dialogFormVisible: this.$store.state.excelShowState,
      buttonLoading: false,
      excel: {
        name: '',
        size: '1200',
        status: 1,
        excel: '',
      },
      rules: {
        name: [
          { required: true, message: '请选择模板文件', trigger: 'blur' }
        ],
        size: [
          { required: true, message: '请输入输出图片宽度', trigger: 'blur' }
        ],
      },
      editExcel: this.$store.state.editExcelStatus,
      placeholder: '',
      formLabelWidth: '100px',
      uploadData: {
        action: '/v1/OrderImport',
        buttonName: '浏览',
        autoUpload: false,
        componentsName: 'addExcel'
      },
    }
  },
  methods: {
    handleClose(){
      this.$store.commit('CHANGE_EXCEL_DATA',{name: '', size: '1200', status: 1, excel: ''});
      this.$store.commit('ADD_EXCEL_SHOW_STATE',false);
      this.$store.commit('CHANGE_EXCEL_EDIT_STATUS','');
      this.buttonLoading = false;
    },
    //添加excel
    addExcel(u_id=sessionStorage.getItem('selectUId')){
      this.buttonLoading = true;
      if(!this.excel.excel){
        this.$message({
          type: 'warning',
          message: '请选择excel进行上传'
        })
        return false;
      }
      let formData = new FormData();
      formData.append('excel',this.excel.excel);
      formData.append('name',this.excel.name);
      formData.append('size',this.excel.size);
      formData.append('status',this.excel.status);
      this.imgRequest(`/v1/home/index/upload?u_id=${u_id}`,formData)
      .then(res => {
        if(res.data.code=='200'){
          this.$message({
            type: 'success',
            message: '上传成功'
          })
        }
        if(res.data.code=='201'){
          this.$message({
            type: 'warning',
            message: '模板文件格式不兼容，请另存为.xls！'
          })
        }
        if(res.data.code=='202'){
          this.$message({
            type: 'warning',
            message: '模板文件格式不兼容，请另存为.xls！'
          })
        }
        this.handleClose();
        this.$store.commit('REFRESH_EXCEL_STATUS')
        this.buttonLoading = false;
      })
    },
    //修改excel信息
    editExcelData(){
        this.buttonLoading = true;
        let formData = new FormData();
      if(this.excel.excel){
        formData.append('excel',this.excel.excel);
      }
        formData.append('id',this.excel.id);
        formData.append('name',this.excel.name);
        formData.append('size',this.excel.size);
        formData.append('status',this.excel.status);
        this.imgRequest(`/v1/home/index/fieldBasicEdit`,formData)
        .then(res => {
          if(res.data.code=='200'){
            this.$message({
              type: 'success',
              message: '修改成功'
            })
          }
          if(res.data.code!='200'){
            this.$message.error('修改失败');
          }
          this.handleClose();
          this.$store.commit('REFRESH_EXCEL_STATUS')
          this.buttonLoading = false;
        })
    },
    //添加前校验
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.editExcelStatus=='add'){
            this.addExcel();
          }
          if(this.editExcelStatus=='edit'){
            this.editExcelData();
          }
        } else {
          return false;
        }
      });
    },
  },
  components: {
    uploadExcel
  },
  computed: {
    excelShowState(){
      return this.$store.state.excelShowState;
    },
    excelData(){
      return this.$store.state.excelData;
    },
    editExcelStatus(){
      return this.$store.state.editExcelStatus;
    },
    addExcelData(){
      return this.$store.state.excel;
    }
  },
  watch: {
    excelShowState: function(newVal,oldVal){
      this.dialogFormVisible = newVal;
    },
    excelData: function(newVal,oldVal){
      this.excel = newVal;
    },
    editExcelStatus: function(newVal,oldVal){
      this.editExcel = newVal;
    },
    addExcelData: function(newVal,oldVal){
      this.excel.excel = newVal.raw;
      this.excel.name = newVal.name;
    },
  }
}
</script>

<style lang="scss" scoped>
.addExcelStyle {
  text-align: left;
}
</style>
